    .timer-menu,
    .project-content,
    .category-content,
    .subcategory-content {
        min-width: 20vw;
        grid-template-columns: 1fr;
        justify-items: center;
        justify-content: center;
        justify-self: center;

        > * {
            margin: auto;
        }
    }

    main {
        padding: calc(2 * var(--size-pri)) calc(0.5 * var(--size-pri));
    }

