lf: left;\n        transition: transform 0.3s ease-in-out;\n    }\n\n    button:hover {\n        transform: scale(1.1);\n    }\n\n}\n\n\n.menu-header {\n    width: 80%;\n    display: flex;\n    gap: calc(2 * var(--size-pri));\n    justify-content: right;\n    padding: calc(2 * var(--size-pri));\n    margin: auto;\n\n    button {\n        display: flex;\n        position: relative;\n        background-color: var(--color-qua);\n        color: white;\n        font-weight: var(--weight-sec);\n        padding: 0.5em 1em;\n        border-radius: var(--radius-pri);\n        transition: background-color 1s,\n            color 1s;\n    }\n\n    button:after,\n    button:before {\n        content: '';\n        position: absolute;\n        left: 0;\n        top: 0;\n        display: inline-block;\n        height: 100%;\n        width: 100%;\n        /* top: -1em;\n        left: -10%; */\n        /* margin-top: 10px; */\n        opacity: 0;\n        transition: opacity 1s,\n            transform 1s;\n    }\n\n    button:before {\n        border-left: 3px solid var(--color-tri);\n        border-right: 3px solid var(--color-tri);\n        -webkit-transform: scale(0, 1);\n        transform: scale(0, 1);\n    }\n\n    button:after {\n        border-bottom: 3px solid var(--color-tri);\n        border-top: 3px solid var(--color-tri);\n        -webkit-transform: scale(1, 0);\n        transform: scale(1, 0);\n    }\n\n    button:hover:after,\n    button:hover:before {\n        opacity: 1;\n        -webkit-transform: scale(1);\n        transform: scale(1);\n        border-radius: var(--radius-pri);\n    }\n\n\n    /* button:hover {\n        /* box-shadow: 0 0.6em 0.5em -0.5em var(--color-tri); */\n    /* background-color: var(--color-pri);\n    color: var(--color-tri) */\n    /* } */\n\n    img {\n        display: none;\n        min-height: 3vh;\n        min-width: 3vh;\n        aspect-ratio: 1 / 1;\n        filter: invert(99%) sepia(0%) saturate(7500%) hue-rotate(154deg) brightness(113